












import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component
export default class ProTabs extends Mixins(TwoWay) {
    @Prop()
    tabs!: any[];

    /**
     * 筛选有效显示的tabs
     */
    get effectiveTabs() {
        const { tabs } = this;
        const result = tabs.filter(
            item => item.label && (item.value || item.value === 0) && item.if !== false
        );

        return result;
    }
    @Watch("currentValue")
    watchCurrentValue(val: any) {
        this.$emit("click", val);
    }
}
