
















































































































import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";
import debounce from "lodash.debounce";

import { DictionaryItem } from "@/mode/dictionary-item";

import ProFilters from "@/components/pro-filters/pro-filters.vue";
import ProTabs from "@/components/pro-tabs/pro-tabs.vue";
import ProTable from "@/components/pro-table/pro-table.vue";
import ProPagination from "@/components/pro-pagination/pro-pagination.vue";

@Component({
    components: {
        ProFilters,
        ProTabs,
        ProTable,
        ProPagination
    }
})
export default class FrameListView extends Mixins(TwoWay) {
    /**筛选器配置 */
    @Prop()
    filters!: any[];

    /**标签配置 */
    @Prop({
        default: () => {
            [];
        }
    })
    tabs!: any[];

    /**表格配置 */
    @Prop({ default: () => new Object() })
    tableOptions!: any;

    /**总数 */
    @Prop({ default: 0 })
    total!: number;

    /** 数据表格方法 */
    @Prop({ default: false })
    getData!: (() => Promise<any>) | false;

    /** 请求loading */
    @Prop()
    requestLoading!: boolean;

    /** 是否阻止默认请求， true 则需要手动调用getData */
    @Prop({ type: Boolean, default: false })
    stopDefaultRequest!: boolean;

    /** 显示 工具栏  */
    @Prop({ type: Boolean, default: true })
    showToolbar!: boolean;

    /** 加载中状态 */
    loading = false;

    /** 更新锁，为真时不触发emit */
    changeLock = false;

    // 全选标记
    checkAllFlag = false;

    /** 主显示区高度 */
    mainHeight = 0;

    /**备份筛选数据**/

    copyCurrentValue = {};

    get indeterminate() {
        const { selected, listData } = this.currentValue;
        if (selected) {
            return selected.length > 0 && selected.length !== listData.length;
        }
        return false;
    }

    get hasTabs() {
        return !!(this.tabs && this.tabs.length);
    }

    /** Slot:Btn中是否包含有效的子节点 */
    // 仅能过滤使用v-if移除所有子节点的情况
    // 使用js移除（例如permission指令）的情况使用css控制样式
    get isBtnSlotHasEffectiveChildrenNode() {
        let result = false;
        const slotBtns = this.$slots.btns;

        if (slotBtns) {
            for (const item of slotBtns) {
                if (!item.isComment) {
                    result = true;
                    break;
                }
            }
        }
        return result;
    }
    // 获取动态具名插槽 的 prop 数组
    get dynamicSlotFilters() {
        return this.dynamicSlot(this.filters);
    }
    /** 动态添加的表格列 */
    // 以tableOptions.columns中子项type==='slot'为判定条件
    get dynamicSlotTableColumns() {
        return this.dynamicSlot(this.tableOptions.columns);
    }
    // 确定是否全选
    @Watch("currentValue.selected")
    watchCurrentValueSelected(newVal: any[]) {
        this.checkAllFlag = newVal.length > 0;
    }

    @Watch("currentValue.filterData", { deep: true })
    handleFilterDataWatch(val: any) {
        if (this.showToolbar) {
            return;
        }
        this.resetPageCurrent();
        this.emitRefresh();
    }

    @Watch("currentValue.pageData", { deep: true })
    handlePageDataWatch() {
        if (this.changeLock) {
            return;
        }
        this.emitRefresh();
    }

    @Watch("currentValue.sortData", { deep: true })
    handleSortDataWatch() {
        if (this.changeLock) {
            return;
        }
        this.emitRefresh();
    }

    @Watch("currentValue.tabsData")
    handleTabsDataWatch() {
        this.resetPageCurrent();
        this.emitRefresh();
    }
    /** 发送刷新事件 */
    emitRefresh = debounce(
        function(this: FrameListView) {
            this.getDataFn();
            this.$emit("refresh");
        },
        700,
        { leading: true }
    );
    dynamicSlot(targetArray: any[]) {
        const result: string[] = [];
        const findItem = (targetArray || []).find((item: any) => item.prop && item.type === "slot");
        findItem && result.push(findItem.prop);

        return result;
    }
    // 页码重置
    resetPageCurrent() {
        this.currentValue.pageData && (this.currentValue.pageData.current = 1);
        this.changeLock = true;
        this.$nextTick(() => {
            this.changeLock = false;
        });
    }
    // 重置
    resetFilterHandler() {
        this.currentValue.filterData = JSON.parse(JSON.stringify(this.copyCurrentValue));
    }
    // 搜索
    searchFilterHandler() {
        this.resetPageCurrent();
        this.emitRefresh();
    }
    /** 计算主显示区大小 */
    calcMainHeight() {
        this.$nextTick(() => {
            this.$refs["pro-table"] &&
                (this.mainHeight = (this.$refs["pro-table"] as Element).clientHeight);
        });
    }
    /** 选择 */
    haneleClickChoose() {
        (this.$refs["pro-table"] as ProTable).toggleAllSelection();
    }

    getDataFn() {
        if (typeof this.getData == "function") {
            try {
                this.loading = true;
                this.getData()
                    .then()
                    .finally(() => {
                        this.loading = false;
                    });
            } catch {
                console.error(
                    'the bind value get-data must be "false" or a function returns Promise'
                );
            }
        }
    }
    /** 添加窗口变化监听 */
    addResizeEventListener() {
        // 窗口变化时重新计算主区大小
        window.addEventListener("resize", this.calcMainHeight);
    }

    /** 移除窗口变化监听 */
    removeResizeEventListener() {
        // 窗口变化时重新计算主区大小
        window.removeEventListener("resize", this.calcMainHeight);
    }
    created() {
        this.addResizeEventListener();
        !this.stopDefaultRequest && this.emitRefresh();
    }
    mounted() {
        this.copyCurrentValue = JSON.parse(JSON.stringify(this.currentValue.filterData));
        this.calcMainHeight();
    }
    updated() {
        this.calcMainHeight();
    }
    beforeDestroy() {
        this.removeResizeEventListener();
    }
}
