/** 字典项数据模型
 * @value   值
 * @label  标签
 * @status  状态
 * @disabled  是否禁用
*/
export class DictionaryItem {
    value: number | string;
    label: number | string;
    status?: number | string;
    disabled?: boolean
    children?: DictionaryItem[];
    constructor(
        value: number | string,
        label: number | string,
        status?: number | string,
        disabled?: boolean,
        children?: DictionaryItem[],
    ) {
        this.value = value;
        this.label = label;
        this.status = status;
        this.disabled = disabled;
        this.children = children;
    }
}
