
























import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import ProFilterItem from "./pro-filter-item.vue";

@Component({
    components: {
        ProFilterItem
    }
})
export default class ProFilters extends Mixins(TwoWay) {
    @Prop()
    filters!: any[];
    /**是否默认展开 */
    @Prop({ type: Boolean, default: true })
    defaulyExpanded!: boolean;

    /** 实际高度  */
    actualHeight = 0;

    // 是否展开
    isExpanded: boolean = false;

    get isOverflow() {
        return this.actualHeight > 70;
    }

    /** 有效的筛选器 */
    get effectiveFilters() {
        const { filters } = this;

        let result: any[] = [];

        if (filters) result = filters.filter(item => item.prop);
        return result;
    }

    /** 当前展示的筛选器 */
    get shownFilters() {
        const { effectiveFilters, isExpanded } = this;

        let result: any[] = [];

        if (effectiveFilters) result = effectiveFilters.filter(item => item.if !== false);
        if (!isExpanded) result = result.splice(0, 3);
        return result;
    }

    get slotedItems() {
        return this.shownFilters.filter(item => item.type === "slot");
    }
    getFilter(index: number) {
        return this.isExpanded ? index >= 0 : index < 3;
    }

    panelCollapse() {
        this.isOverflow && (this.isExpanded = !this.isExpanded);
    }
    /** 计算实际高度 */
    calcActualHeight() {
        this.$nextTick(() => {
            // 为什么要异步获取？ // 同步获取就不对……
            this.$refs.filters &&
                (this.actualHeight = (this.$refs.filters as Element).clientHeight);
        });
    }

    mounted() {
        this.isExpanded = !!this.defaulyExpanded;
        this.calcActualHeight();
        window.addEventListener("resize", this.calcActualHeight);
    }

    updated() {
        this.calcActualHeight();
    }

    beforeDestory() {
        window.removeEventListener("resize", this.calcActualHeight);
    }

    @Watch("isOverflow")
    watchIsOverflow(newVal: boolean) {
        !newVal && (this.isExpanded = false);
    }
}
